import {mapGetters, mapState} from 'vuex';
import appTabNav from "../../../components/basic-component/app-tab-nav/app-tab-nav.vue";

export default {
    data() {
        return {
            show: false,
            statics: {
                all_price: '',
                stay_price: '',
            },
            tabList: [
                {id:1, name: '概况'},
                {id:2, name: '明细'}
            ],
            activeTab: 1,
            type: '',
            page: 1,
            list: [],
            detail: []
        }
    },
    components: {
        "app-tab-nav": appTabNav
    },
    computed: {
        ...mapGetters('mallConfig', {
            getTheme: 'getTheme',
        }),
        ...mapState({
            stock: state => state.mallConfig.__wxapp_img.stock,
            bonusImg: state => state.mallConfig.__wxapp_img.bonus,
            userInfo: state => state.user.info,
        })
    },
    onLoad(options) { this.$commonLoad.onload(options);
        this.$showLoading({
            type: 'global',
            text: '加载中...'
        });
        this.getStatus();
    },
    onReachBottom() {
        this.page++;
        if(this.activeTab == 2) {
            this.getDetail();
        }else {
            this.getList();
        }
    },
    methods: {
        tabStatus(e) {
            let id = e.currentTarget.dataset.id;
            this.show = false;
            this.activeTab = id;
            this.page = 1;
            if(this.detail.length == 0) {
                this.$showLoading({
                    type: 'global',
                    text: '加载中...'
                });
            }
            if(this.activeTab == 2) {
                this.getDetail();
            }else {
                this.getList();
            }
        },
        toggle(type) {
            this.type = type;
            this.show = false;
            this.page = 1;
            this.getDetail();
        },
        getDetail() {
            let that = this;
            that.$request({
                url: that.$api.community.profit_detail,
                data: {
                    type: this.type,
                    page: this.page
                }
            }).then(response=>{
                uni.hideLoading();
                that.$hideLoading();
                if(response.code == 0) {
                    that.statics = response.data.data;
                    if(that.page > 1) {
                        that.detail = that.detail.concat(response.data.list);
                    }else {
                        that.detail = response.data.list;
                    }
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                uni.hideLoading();
                that.$hideLoading();
            });
        },
        getList() {
            let that = this;
            that.$request({
                url: that.$api.community.profit_list,
                data: {
                    page: this.page
                }
            }).then(response=>{
                uni.hideLoading();
                that.$hideLoading();
                if(response.code == 0) {
                    if(that.page > 1) {
                        that.list = that.list.concat(response.list);
                    }else {
                        that.list = response.list;
                    }
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                uni.hideLoading();
                that.$hideLoading();
            });
        },
        getStatus() {
            let that = this;
            that.$request({
                url: that.$api.community.index,
            }).then(response=>{
                if(response.code == 0) {
                    this.setting = response.data.setting;
                    if(response.data.middleman.id > 0) {
                        this.middleman = response.data.middleman;
                        this.apply_at = this.middleman.apply_at.substring(0,10)
                        this.getList();
                    }else {
                        uni.showToast({
                            title: '您还不是团长，现在前往申请页面',
                            icon: 'none',
                            duration: 1000
                        });
                        setTimeout(function(){
                            uni.redirectTo({
                                url: '/plugins/community/apply/apply'
                            });
                        },1000)
                    }
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
            });
        }
    }
}
